.portfolio {
	text-align: center;
	padding: 110px 0 120px;

	&-title {
		font-family: 'Montserrat', sans-serif;
		color: var(--title-color);
		font-size: 45px;
		line-height: 1.2;
		margin-bottom: 30px;
		text-transform: uppercase;
		letter-spacing: 0;
	}

	&-subtitle {
		font-size: 15px;
		font-weight: 400;
		letter-spacing: 0.05em;
		color: var(--subtitle-color);
		max-width: 670px;
		margin: 0 auto;
		margin-bottom: 78px;
	}

	&-list {
		display: flex;
		flex-wrap: wrap;
		gap: 20px;
	}

	&-item {
		display: flex;
		width: calc((100% - 60px) / 4);
		position: relative;
		height: 320px;

		&:hover .portfolio-link {
			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}

	&-img {
		flex: 1;
		object-fit: cover;
	}

	&-link {
		color: var(--title-color);
		box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.4);
		width: 85px;
		height: 85px;
		border-radius: 50%;
		background-color: var(--white);
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 24px;
		line-height: 1.46;
		font-weight: 500;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-70%, -50%);
		opacity: 0;
		transition: all var(--transit);

		&:hover {
			box-shadow: 0 0 0 20px rgba(255, 255, 255, 0.4);
		}
	}
}
