*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

html, body {
  min-height: 100%;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, figcaption, blockquote {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input, button, textarea, select {
  font: inherit;
  background-color: #0000;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  color: inherit;
}

a {
  color: inherit;
  font-size: inherit;
  line-height: inherit;
  text-decoration: none;
}

svg {
  fill: inherit;
  stroke: inherit;
  stroke-width: inherit;
}

:root {
  --accent: #ffd541;
  --title-color: #151515;
  --default-color: #323232;
  --hover-purple: #7240b2;
  --default-bg: #343333;
  --white: #fff;
  --logo-color: #234424;
  --subtitle-color: #151515cc;
  --shadow-color: #ffffff80;
  --transit: .3s ease-in-out;
}

@font-face {
  font-family: Montserrat;
  font-weight: 900;
  font-style: normal;
  src: url("Montserrat-Black.518e57c7.woff2") format("woff2"), url("Montserrat-Black.15224e66.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Montserrat;
  font-weight: 700;
  font-style: normal;
  src: url("Montserrat-Bold.447a2a3e.woff2") format("woff2"), url("Montserrat-Bold.faadd583.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Montserrat;
  font-weight: 600;
  font-style: normal;
  src: url("Montserrat-SemiBold.81b97deb.woff2") format("woff2"), url("Montserrat-SemiBold.f49214a7.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Montserrat;
  font-weight: 500;
  font-style: normal;
  src: url("Montserrat-Medium.3e1d2f02.woff2") format("woff2"), url("Montserrat-Medium.8e8ffb82.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  font-weight: 600;
  font-style: normal;
  src: url("Poppins-SemiBold.cbffc196.woff2") format("woff2"), url("Poppins-SemiBold.5e82680a.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  font-weight: 500;
  font-style: normal;
  src: url("Poppins-Medium.64bbd6ea.woff2") format("woff2"), url("Poppins-Medium.80ddb498.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  font-weight: 400;
  font-style: normal;
  src: url("Poppins-Regular.7fa61b9f.woff2") format("woff2"), url("Poppins-Regular.56c8080e.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  font-weight: 300;
  font-style: normal;
  src: url("Poppins-Light.1355d88d.woff2") format("woff2"), url("Poppins-Light.d904df83.woff") format("woff");
  font-display: swap;
}

body {
  letter-spacing: .02em;
  color: var(--default-color);
  background-color: var(--white);
  flex-direction: column;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.67;
  display: flex;
}

.main {
  flex-grow: 1;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header-sticky {
  background: #343333;
  border-bottom-color: #343333;
  box-shadow: 0 0 15px #15151559;
}

.menu__btn {
  z-index: 2;
  border: none;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-left: 20px;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media (min-width: 980px) {
  .menu__btn {
    display: none;
  }
}

.logo {
  color: var(--white);
  align-items: center;
  font-family: Montserrat, sans-serif;
  font-size: clamp(20px, 3vw, 40px);
  font-weight: 600;
  display: flex;
}

.logo-link {
  text-overflow: ellipsis;
  white-space: nowrap;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.logo-img {
  width: clamp(30px, 4vw, 60px);
  margin-right: 10px;
}

.hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

#scrollUpButton {
  background-color: var(--hover-purple);
  color: #fff;
  cursor: pointer;
  z-index: 100;
  transition: transform var(--transit);
  border: none;
  border-radius: 5px;
  width: 60px;
  height: 60px;
  position: fixed;
  bottom: 40px;
  right: 40px;
  transform: translate3d(0, 100px, 0);
}

#scrollUpButton:before {
  content: "˄";
  text-align: center;
  font-size: 24px;
  line-height: 40px;
}

.header {
  padding: 35px 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav {
  width: 100%;
  display: flex;
}

.header-links-wrap {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 570px;
  margin-left: auto;
  display: flex;
}

.header-nav-links {
  align-items: center;
  column-gap: 50px;
  padding-right: 20px;
  display: flex;
}

.header-nav-link {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: var(--white);
  transition: color var(--transit);
  font-size: 15px;
  font-weight: 500;
  line-height: 1.2;
}

.header-nav-link:hover, .header-nav-link:focus, .header-nav-link:active, .header-nav-link--active {
  color: var(--accent);
}

.header-social-links {
  column-gap: 25px;
  display: flex;
}

.header-social-item {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-social-link {
  height: 100%;
}

.header .social-icon {
  transition: fill var(--transit);
  fill: #ffffff80;
}

.header .social-icon:hover {
  fill: var(--white);
}

.hero {
  background-image: linear-gradient(#000000a6, #000000a6), url("bg-ps-main.181f8a80.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
}

.hero-inner {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero-title {
  text-transform: uppercase;
  color: var(--white);
  letter-spacing: .01em;
  text-shadow: 0 4px 12px #0000004d;
  margin-bottom: 40px;
  font-family: Montserrat, sans-serif;
  font-size: 75px;
  font-weight: 900;
  line-height: 1.2;
  position: relative;
}

.hero-title:before {
  content: "";
  background-color: #fff;
  width: 62px;
  height: 3px;
  position: absolute;
  bottom: -19px;
  left: calc(50% - 30px);
}

.hero-subtitle {
  color: var(--white);
  letter-spacing: -.03em;
  font-size: 18px;
  font-weight: 400;
}

.appointment {
  background-color: var(--default-color);
  color: var(--white);
  padding: 60px 0;
}

.appointment-form {
  align-items: center;
  height: 81px;
  display: flex;
}

.appointment-label {
  text-transform: uppercase;
  flex-direction: column;
  padding: 0 12px;
  font-weight: 400;
  display: flex;
}

.appointment-input {
  color: var(--white);
  border: none;
  border-bottom: 1px solid #ffffff1a;
  outline: none;
  width: 274px;
  padding: 14px 16px 16px 0;
  font-size: 18px;
}

.appointment-input::placeholder {
  color: #fff;
  font-size: 18px;
}

.appointment-input .notvalid {
  border-color: #f5543f;
}

.appointment-submit-btn {
  background-color: var(--white);
  color: var(--default-color);
  text-transform: uppercase;
  letter-spacing: .002em;
  transition: color var(--transit), background-color var(--transit), border-color var(--transit);
  border-color: #0000;
  margin-top: 12px;
  margin-left: 27px;
  padding: 18px 35px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
}

.appointment-submit-btn:hover, .appointment-submit-btn:focus, .appointment-submit-btn:active {
  color: var(--white);
  background-color: var(--hover-purple);
}

.featured {
  text-align: center;
  padding: 115px 0;
}

.featured-title {
  text-transform: uppercase;
  color: var(--title-color);
  letter-spacing: 0;
  margin-bottom: 18px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  line-height: 1.2;
}

.featured-subtitle {
  color: var(--subtitle-color);
  max-width: 670px;
  margin: 0 auto 78px;
  font-size: 15px;
}

.featured-services {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.featured-item:hover .featured-img_wrap:before {
  opacity: 1;
}

.featured-item:hover .featured-btn {
  opacity: 1;
  transform: translate(-50%, 65%);
}

.featured-img_wrap {
  max-width: 370px;
  height: 390px;
  display: flex;
  position: relative;
}

.featured-img_wrap:before {
  content: "";
  opacity: 0;
  transition: opacity var(--transit);
  background: linear-gradient(#15151500 38.12%, #1515155d 100%);
  position: absolute;
  inset: 0;
}

.featured-img {
  object-fit: cover;
  flex: 1;
}

.featured-btn {
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--hover-purple);
  opacity: 0;
  transition: all var(--transit);
  padding: 12px 25px;
  font-size: 13px;
  font-weight: 500;
  position: absolute;
  top: 62%;
  left: 50%;
  transform: translateX(-50%);
}

.featured-btn:hover {
  color: var(--hover-purple);
  background-color: var(--white);
}

.featured-title_link {
  text-transform: uppercase;
  color: var(--title-color);
  margin-bottom: 10px;
  font-family: Montserrat, sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.18;
  display: block;
}

.featured-text {
  letter-spacing: .05em;
  letter-spacing: .05em;
  text-transform: uppercase;
  color: var(--hover-purple);
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  line-height: 1.14;
}

.featured-name {
  padding: 25px;
}

.promotion {
  text-align: center;
  color: var(--white);
  background-image: linear-gradient(#000000a6, #000000a6), url("rednails-bg.e9cdabf3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  padding: 135px 0;
}

.promotion-inner {
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.promotion-subtitle {
  font-family: Montserrat, sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.18;
}

.promotion-title {
  margin-bottom: 30px;
  font-family: Montserrat, sans-serif;
  font-size: 55px;
  font-weight: 700;
  line-height: 1.56;
}

.promotion-link {
  background-color: var(--white);
  letter-spacing: .002em;
  color: var(--default-color);
  transition: color var(--transit), background-color var(--transit);
  width: 190px;
  padding: 19px 35px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
}

.promotion-link:hover, .promotion-link:focus, .promotion-link:active {
  color: var(--white);
  background-color: var(--hover-purple);
}

.portfolio {
  text-align: center;
  padding: 110px 0 120px;
}

.portfolio-title {
  color: var(--title-color);
  text-transform: uppercase;
  letter-spacing: 0;
  margin-bottom: 30px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  line-height: 1.2;
}

.portfolio-subtitle {
  letter-spacing: .05em;
  color: var(--subtitle-color);
  max-width: 670px;
  margin: 0 auto 78px;
  font-size: 15px;
  font-weight: 400;
}

.portfolio-list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.portfolio-item {
  width: calc(25% - 15px);
  height: 320px;
  display: flex;
  position: relative;
}

.portfolio-item:hover .portfolio-link {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.portfolio-img {
  object-fit: cover;
  flex: 1;
}

.portfolio-link {
  color: var(--title-color);
  background-color: var(--white);
  opacity: 0;
  transition: all var(--transit);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 85px;
  height: 85px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.46;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-70%, -50%);
  box-shadow: 0 0 0 10px #fff6;
}

.portfolio-link:hover {
  box-shadow: 0 0 0 20px #fff6;
}

.about {
  background-color: var(--default-color);
  color: var(--white);
  z-index: 1;
  padding: 135px 0;
  position: relative;
}

.about-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.about-img_wrap {
  width: 483px;
  height: 400px;
  display: flex;
  position: relative;
}

.about-img_wrap:after {
  content: "";
  z-index: -1;
  background-color: #ffffff0d;
  width: 488px;
  height: 495px;
  position: absolute;
  top: -46px;
  left: 65px;
}

.about-image {
  object-fit: cover;
  flex: 1;
  display: flex;
}

.about-wrapper {
  letter-spacing: -.03em;
  max-width: 470px;
  font-size: 15px;
  font-weight: 300;
}

.about-title {
  text-transform: uppercase;
  margin-bottom: 32px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 1.2;
}

.about-text {
  margin-bottom: 19px;
}

.about-text.bottom {
  margin-bottom: 46px;
}

.about-link {
  letter-spacing: .002em;
  text-transform: uppercase;
  color: var(--default-color);
  background-color: var(--white);
  transition: color var(--transit), background-color var(--transit);
  justify-content: center;
  align-items: center;
  width: 190px;
  padding: 18px 35px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
  display: inline-flex;
}

.about-link:hover, .about-link:focus, .about-link:active {
  background-color: var(--hover-purple);
  color: var(--white);
}

.equipment {
  padding: 130px 0 120px;
}

.equipment-inner {
  text-align: center;
}

.equipment-list {
  display: flex;
}

.equipment-item {
  flex: none;
  width: 33.3333%;
  padding: 0 26px;
}

.equipment-item:hover .woman:after {
  transform: rotate(175deg)translate(15px, 15px);
}

.equipment-item:hover .chair:after {
  transform: translate(-45px, 2px);
}

.equipment-item:hover .phone:after {
  transform: rotate(-90deg)translateY(-40px);
}

.equipment-icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 60px;
  height: 60px;
  margin: 0 auto 30px;
  display: inline-block;
  position: relative;
}

.equipment-icon:after {
  content: "";
  z-index: -1;
  transition: all .6s;
  position: absolute;
  top: 33%;
  left: 82%;
}

.equipment-icon.woman {
  background-image: url("woman.3f762cff.svg");
}

.equipment-icon.woman:after {
  will-change: transform;
  border: 10px solid #0000;
  border-top-width: 0;
  border-bottom: 18px solid #c9c0d5;
  width: 0;
  height: 0;
  transform: rotate(137.76deg)translate(0);
}

.equipment-icon.chair {
  background-image: url("salon_chair.e4aa69bc.svg");
}

.equipment-icon.chair:after {
  will-change: transform;
  background-color: #c9c0d5;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  top: 25%;
  left: 74%;
}

.equipment-icon.phone {
  background-image: url("phone.b7153168.svg");
}

.equipment-icon.phone:after {
  will-change: transform;
  background-color: #c9c0d5;
  width: 16px;
  height: 16px;
  transform: translateY(0)rotate(45deg);
}

.equipment-title {
  text-transform: uppercase;
  letter-spacing: 0;
  color: var(--title-color);
  transition: color var(--transit);
  margin-bottom: 15px;
  font-family: Montserrat, sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.18;
  display: block;
}

.equipment-title:hover, .equipment-title:focus, .equipment-title:active {
  color: var(--hover-purple);
}

.equipment-text {
  letter-spacing: -.03em;
  color: #15151599;
  max-width: 380px;
  font-size: 15px;
  font-weight: 300;
  line-height: 1.66;
}

.footer {
  background-color: var(--default-bg);
  padding-top: clamp(60px, 8vw, 105px);
  padding-bottom: clamp(60px, 8vw, 120px);
}

.footer-inner {
  color: var(--white);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer .logo {
  margin-bottom: 50px;
  font-size: clamp(40px, 5vw, 60px);
}

.footer-links {
  margin-bottom: 50px;
  display: flex;
}

.footer-item {
  padding: 0 20px;
  position: relative;
}

.footer-item:not(:last-child):after {
  content: "";
  background-color: var(--shadow-color);
  width: 1px;
  height: 25px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.footer-link {
  text-transform: uppercase;
  transition: color var(--transit);
  font-size: 15px;
}

.footer-link:hover, .footer-link:focus, .footer-link:active {
  color: var(--hover-purple);
}

.footer .header-social-links {
  fill: var(--white);
  margin-bottom: 90px;
}

.footer .header-social-item {
  transition: var(--transit);
  background-color: #ffffff0d;
  border-radius: 50%;
  width: 48px;
  height: 48px;
}

.footer .header-social-item:hover, .footer .header-social-item:focus, .footer .header-social-item:active {
  background-color: var(--hover-purple);
}

.footer .header-social-link {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.footer-copyright {
